"use strict";

var modalSuccess = {
  Title: "Payment success",
  Message: "You have successfully made a purchase via paypal, please wait while redirected to download page in fews seconds ...",
  Type: "modal-success",
  Icon: "fas fa-check",
  Button: "btn-success"
};
var modalError = {
  Title: "Payment error",
  Message: "An error occured before your transaction finished, try another attempt to complete your purchase",
  Type: "modal-danger",
  Icon: "fas fa-ban",
  Button: "btn-danger"
};

function ShowPayPalButton() {
  setTimeout(function () {
    paypal.Button.render({
      env: 'sandbox',
      // Or 'production'
      // Set up the payment:
      // 1. Add a payment callback
      payment: function (data, actions) {
        // 2. Make a request to your server
        var cartDetail = GetFromLocalStorege("CartDetail");
        var token = GetFromLocalStorege("authToken");
        var authorization = `Bearer ${token}`;
        return actions.request.post('/api/paypal/createpayment/', {
          "orderId": "noid",
          "cartDetail": cartDetail
        }, {
          "headers": {
            "Authorization": authorization
          }
        }).then(function (res) {
          return res.id;
        });
      },
      // Execute the payment:
      // 1. Add an onAuthorize callback
      onAuthorize: function (data, actions) {
        // 2. Make a request to your server
        var cartDetail = GetFromLocalStorege("CartDetail");
        var token = GetFromLocalStorege("authToken");
        var authorization = `Bearer ${token}`;
        return actions.request.post('/api/paypal/executepayment/', {
          "orderId": data.orderID,
          "cartDetail": cartDetail
        }, {
          "headers": {
            "Authorization": authorization
          }
        }).then(function (res) {
          if (res.status === "success") {
            removeBuyingItemsFromCart();
            showModalDialog(JSON.stringify(modalSuccess));
            goToDownload();
          } else {
            showModalDialog(JSON.stringify(modalError));
          }
        });
      }
    }, '#paypal-button');
  }, 1000);
}

;